.modalLogout {
  position: absolute;
  top: 20px;
  right: 50%;
  left: 50%;
  transform: translateX(-50%);
  width: 17rem;
  height: 9rem;
  border-radius: 20px;
  background-color: var(--color-brand);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}
.header-3 {
  text-align: center;
  color: var(--color-secondary);
  font-size: 1.2rem;
}
.header-4 {
  font-size: 0.8rem;
  font-family: 'Circe';
  text-transform: uppercase;
  color: var(--color-secondary);
}
.btn-wrapper {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}
.button {
  font-family: 'Circe';
  width: 100px;
  border: none;
  background-color: var(--background-btn);
  border: 1px solid var(--color-brand);
  border-radius: 25px;
  color: var(--color-secondary);
  font-size: 1.1rem;
  transition: var(--transitionDefault);
  z-index: 1;
}
.button:hover {
  transition: var(--transitionDefault);
  border: 1px solid var(--color-secondary);
  background-color: var(--background-icon);
}
.btn-icon {
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  background-color: var(--color-secondary);
  transition: var(--transitionDefault);
}
.btn-icon:is(:focus, :hover, ) {
  transition: var(--transitionDefault);
  border-radius: 50%;
  background-color: var(--background-btn);
}
.background {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}
.wrapper_modalLogo {
  color: var(--color-dimmed);
  display: flex;
  justify-content: center;
  align-items: center;
  padding-right: 20px;
}

.exit {
  display: none;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.spam {
  display: none;
}
@media screen and (min-width: 768px) {
  .wrapper_modalLogo {
    padding-right: 32px;
  }
  .wrapper_modalLogo > li:nth-child(1) {
    border-right: 1px solid var(--color-dimmed);
    padding-right: 12px;
  }
  .spam {
    display: flex;
  }
}
@media screen and (min-width: 1280px) {
  .wrapper_modalLogo {
    gap: 12px;
    padding-right: 16px;
  }
}
